

const initialState = {
   isLoading : false,
}

const uiReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SHOW_LOADING_BAR':
      return { ...state, isLoading: true }
    case 'HIDE_LOADING_BAR':
      return { ...state, isLoading: false }
    default:
      return state
  }
}

export default uiReducer
